import React from 'react';
import { Easing } from '../utils/easing';

function input(type, preset, key, onChange, options = {}) {
  return (
    <tr>
      <td style={{ width: 108 }}>
        <strong>
          <label htmlFor={key}>{key}</label>
        </strong>
      </td>
      <td>
        <input
          id={key}
          type={type}
          value={preset[key]}
          checked={type === 'checkbox' ? preset[key] : false}
          onChange={e =>
            onChange({
              [key]:
                type === 'checkbox'
                  ? e.target.checked
                  : type === 'number'
                  ? e.target.value === ''
                    ? 0
                    : parseFloat(e.target.value)
                  : e.target.value,
            })
          }
          {...options}
        />
      </td>
    </tr>
  );
}

function select(value, key, options, onChange) {
  return (
    <tr>
      <td>
        <strong>
          <label htmlFor={key}>{key}</label>
        </strong>
      </td>
      <td>
        <select
          id={key}
          value={value}
          onChange={e => onChange({ [key]: e.target.value })}
        >
          {' '}
          {options.map(option => (
            <option key={option} value={option}>
              {option}
            </option>
          ))}
        </select>
      </td>
    </tr>
  );
}

//////////////////////////////////////////////////

