import React from 'react';
import { Easing } from '../utils/easing';
import RNG from '../utils/random';
import {
  rotationMatrix,
  lerp,
  spiral,
  rotate,
  polarToCart,
  cartToPolar,
  polarToCart3d,
} from '../utils/math';

const STATIC_ARM_CURVE = 2;
const STATIC_ARM_SCALE_RATIO = 28;
const ANIMATION_SPEED_MULTIPLIER = 0.0001;
const GLOW_RATIO = 10;

export function draw(offscreenCanvas, finalCtx, preset, time = 0) {
  const ctx = offscreenCanvas.getContext('2d');
  const canvasSize = ctx.canvas.width;
  const getRandomPosition =
    preset.type === 'spiral'
      ? getSpiralPositionRandomizer(preset, canvasSize / 2, time)
      : getGloburalPositionRandomizer(preset, canvasSize / 2, time);
  const getRandomStyle = getStyleRandomizer(preset);
  const animationMatrix = rotationMatrix(0, time, 0);
  const cameraMtrix = rotationMatrix(
    preset.cameraX,
    preset.cameraZ,
    preset.cameraY
  );

  ctx.clearRect(0, 0, canvasSize, canvasSize);

  ctx.save();
  ctx.translate(canvasSize / 2, canvasSize / 2);
  for (let i = 0; i < preset.stars; i++) {
    let { x, y, z } = getRandomPosition();
    const animationRotatedVector = rotate({ x, y, z }, animationMatrix);
    const cameraRotatedVector = rotate(
      {
        x: animationRotatedVector.x,
        y: animationRotatedVector.y,
        z: animationRotatedVector.z,
      },
      cameraMtrix
    );
    const { size, color } = getRandomStyle();
    drawStar(ctx, cameraRotatedVector, size, color);
  }
  ctx.restore();

  finalize(offscreenCanvas, finalCtx, canvasSize, preset);
}

function getStyleRandomizer(preset) {
  const styleRNG = new RNG(preset.seed);
  const colors = [
    preset.starColor0,
    preset.starColor1,
    preset.starColor2,
    preset.starColor3,
    preset.starColor4,
  ];
  return () => {
    return {
      size: lerp(preset.minStarSize, preset.maxStarSize, styleRNG.random()),
      color: colors[Math.floor(styleRNG.random() * colors.length)],
    };
  };
}

function getGloburalPositionRandomizer(preset, radius) {
  const positionsRNG = new RNG(preset.seed);
  const ease = Easing[preset.density];
  return () => {
    const r = lerp(0, radius, ease(Math.sqrt(positionsRNG.random())));
    const t = lerp(0, Math.PI * 2, positionsRNG.random());
    const globularCoords = polarToCart(r, t);
    const spreadR = lerp(
      0,
      preset.spread,
      Easing[preset.spreadShape](positionsRNG.random())
    );
    const spreadT1 = lerp(0, Math.PI * 2, positionsRNG.random());
    const spreadT2 = lerp(0, Math.PI * 2, positionsRNG.random());
    const spreadCoords = polarToCart3d(spreadR, spreadT1, spreadT2);
    return {
      x: globularCoords.x + spreadCoords.x,
      y: globularCoords.y + spreadCoords.y,
      z:
        getZPosition(preset, positionsRNG, radius, r) +
        spreadCoords.z * preset.heightSpread,
    };
  };
}

function getSpiralPositionRandomizer(preset, radius) {
  const positionsRNG = new RNG(preset.seed);
  const ease = Easing[preset.density];
  return () => {
    const spiralCart = spiral(
      STATIC_ARM_CURVE,
      preset.armsCurve,
      ease(positionsRNG.random()) * (radius / STATIC_ARM_SCALE_RATIO)
    );
    const spiralPolar = cartToPolar(spiralCart.x, spiralCart.y);
    const arm = Math.floor(positionsRNG.random() * preset.arms);
    const armAngleTransform = 2 * Math.PI * (arm / preset.arms);
    const spiralCoords = polarToCart(
      spiralPolar.r,
      spiralPolar.t + armAngleTransform
    );
    const spreadR = lerp(
      0,
      preset.spread,
      Easing[preset.spreadShape](positionsRNG.random())
    );
    const spreadT1 = lerp(0, Math.PI * 2, positionsRNG.random());
    const spreadT2 = lerp(0, Math.PI * 2, positionsRNG.random());
    const spreadCoords = polarToCart3d(spreadR, spreadT1, spreadT2);
    return {
      x: spiralCoords.x + spreadCoords.x,
      y: spiralCoords.y + spreadCoords.y,
      z:
        getZPosition(preset, positionsRNG, radius, spiralPolar.r) +
        spreadCoords.z * preset.heightSpread,
    };
  };
}

function getZPosition(preset, rng, radius, starR) {
  const ratio = 1 - Easing[preset.sideShape](starR / radius);
  const position = ratio * rng.random() * preset.height;
  const side = rng.random() > 0.5 ? 1 : -1;
  return position * side;
}

function drawStar(ctx, { x, y }, size, color) {
  ctx.fillStyle = color;
  ctx.fillRect(x - size / 2, y - size / 2, size, size);
}

function finalize(offscreenCanvas, ctx, canvasSize, preset) {
  ctx.globalCompositeOperation = 'source-over';
  ctx.filter = 'none';
  ctx.fillStyle = preset.spaceColor;
  ctx.fillRect(0, 0, canvasSize, canvasSize);

  if (navigator.userAgent.toLowerCase().indexOf('safari/') === -1) {
    ctx.filter = `blur(${preset.backgroundGlow}px)`;
    ctx.drawImage(offscreenCanvas, 0, 0);

    ctx.globalCompositeOperation = 'screen';
    ctx.filter = `blur(${preset.backgroundGlow / GLOW_RATIO}px)`;
    ctx.drawImage(offscreenCanvas, 0, 0);

    ctx.globalCompositeOperation = 'lighten';
    ctx.filter = `blur(${preset.starGlow}px)`;
    ctx.drawImage(offscreenCanvas, 0, 0);

    ctx.globalCompositeOperation = 'overlay';
    ctx.filter = `blur(${preset.coreBlur}px)`;
    const gradient = ctx.createRadialGradient(
      canvasSize / 2,
      canvasSize / 2,
      0,
      canvasSize / 2,
      canvasSize / 2,
      preset.coreSize / 2
    );
    gradient.addColorStop(0, 'white');
    gradient.addColorStop(0.5, preset.coreColor);
    gradient.addColorStop(1, 'transparent');
    ctx.fillStyle = gradient;
    ctx.fillRect(
      (canvasSize - preset.coreSize) / 2,
      (canvasSize - preset.coreSize) / 2,
      preset.coreSize,
      preset.coreSize
    );
  }

  ctx.globalCompositeOperation = 'screen';
  ctx.filter = 'none';
  ctx.drawImage(offscreenCanvas, 0, 0);
}

//////////////////////////////////////////////////

let time = 0;

export default function Canvas({ size, preset, canvasRef }) {
  const offscreenCanvasRef = React.useRef(null);

  React.useLayoutEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    const offscreenCanvas = offscreenCanvasRef.current;
    let raf = null;

    const onFrame = () => {
      if (preset.animated) {
        time += (preset.speed || 0) * ANIMATION_SPEED_MULTIPLIER;
        draw(offscreenCanvas, ctx, preset, time);
      } else if (time === 0) {
        draw(offscreenCanvas, ctx, preset, time);
      }
      loopRaf();
    };

    const loopRaf = () => {
      raf = window.requestAnimationFrame(onFrame);
    };

    const stopRaf = () => {
      window.cancelAnimationFrame(raf);
    };

    // draw(ctx, preset, time);
    onFrame();

    return stopRaf;
  }, [preset, canvasRef]);

  return (
    <>
      <canvas
        className="offscreen-canvas"
        ref={offscreenCanvasRef}
        width={size}
        height={size}
      />
      <canvas className="canvas" ref={canvasRef} width={size} height={size} />
    </>
  );
}
