import React from 'react';
import Canvas from './Canvas';
import Inputs from './Inputs';
import { saveAs } from 'file-saver';

const DEFAULT_PRESETS = [
  {
    name: 'default',
    seed: 1234,
    stars: 30000,
    spaceColor: '#020e26',
    starColor0: '#EEEDBE',
    starColor1: '#0388A6',
    starColor2: '#025059',
    starColor3: '#E9F2EB',
    starColor4: '#7CA67B',
    minStarSize: 0.8,
    maxStarSize: 2.3,
    density: 'easeInQuad',
    spread: 675,
    spreadShape: 'easeInCubic',
    sideShape: 'easeOutCubic',
    heightSpread: 0.1,
    height: 25,
    cameraX: 0.5,
    cameraY: 1.0,
    cameraZ: 2.0,
    backgroundGlow: 50,
    starGlow: 10,
    animated: true,
    speed: 20,
    coreColor: '#98C0E6',
    coreSize: 600,
    coreBlur: 30,
    type: 'spiral',
    arms: 2,
    armsCurve: 0.275,
  },
];

const CANVAS_SIZE = 1500;

export default function App() {
  const canvasRef = React.useRef(null);

  React.useEffect(() => {
    window.mixpanel.track('app-loaded');
  });

  const [preset, setPresetValue] = React.useState(DEFAULT_PRESETS[0]);

  const onSave = () => {
    canvasRef.current.toBlob(blob => {
      saveAs(blob, `galaxy-${preset.name}.png`);
    });
  };

  return (
    <div className="layout">
      <Canvas canvasRef={canvasRef} size={CANVAS_SIZE} preset={preset} />
      <Inputs
        presets={['default']}
        preset={preset}
        onSave={onSave}
        onChange={value => {
          window.mixpanel.track('input-changed', value);
          setPresetValue({ ...preset, ...value });
        }}
        onChangePreset={preset => {}}
      />
    </div>
  );
}
